<template>
  <div class="main">
    <div class="row" v-if="HomeWork">
      <div class="container coursePageAll">
        <div class="sayHello">
          <h3><span>{{$user.user.School}}{{$user.user.Class}}</span>{{ $user.user.Name }}<span>您好</span></h3>
          <p>歡迎使用故宮線上學校學生專區</p>
        </div>
        <div class="goBackCourse">
          <router-link :to="`/lesson/${Lesson.LessonID}`">
            <img src="/img/goBack.svg" />返回課程</router-link
          >
        </div>
        <h3 class="pageTitle">{{ Lesson.LessonName }} - 課後習題</h3>
        <div class="examList">
          <ol>
            <div class="scoreFlag" v-if="MyHomeWork">
              <span>答對題數</span>
              <h2>
                {{ MyHomeWork.CorrectCount
                }}<span>{{ MyHomeWork.TotalCount }}</span>
              </h2>
            </div>
            <li
              class="examListItem"
              v-for="(q, qi) in HomeWork.Question"
              :key="q.Question"
            >
              {{ q.Question }}
              <div class="Q_choose">
                <label
                  v-for="(a, i) in q.Answers"
                  :key="a"
                  :class="{
                    answerYes:
                      HomeWork.Data && HomeWork.Data[qi].CorrectAnswer == i,
                  }"
                >
                  <input
                    type="radio"
                    :value="i"
                    :name="'ans-' + qi"
                    v-model="MyAnswers[qi]"
                    v-show="a"
                  />{{ a }}</label
                >
              </div>
              <div
                class="answer"
                v-if="
                  HomeWork.Data &&
                  HomeWork.Data[qi].CorrectAnswer != MyAnswers[qi]
                "
              >
                <h4>
                  正確答案 : 答案 {{ HomeWork.Data[qi].CorrectAnswer + 1 }}
                </h4>
                <p>解答說明 :{{ HomeWork.Data[qi].Description }}</p>
              </div>
            </li>
          </ol>
          <div class="submitBtn" v-if="HomeWork.Data">
            <router-link to="/student/class/my">回課程列表</router-link>
            <router-link :to="`/lesson/${Student.Next}`" v-if="Student && Student.Next"
              >下個課程</router-link
            >
          </div>
          <div class="submitBtn" v-else>
            <a href="javascript:void(0);" @click="submit">完成交卷</a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import moment from "moment";
export default {
  data() {
    return {
      HomeWork: null,
      Lesson: null,
      MyAnswers: [],
      MyHomeWork: null,
      Student: null,
    };
  },
  mounted() {
    this.setBase("sSpace page");
    this.load();
  },
  methods: {
    async load() {
      var response = await this.$api.getHomeWork(this.$route.params.id);
      this.Lesson = response.Lesson;
      this.HomeWork = response.HomeWork;
      this.HomeWork.Question.forEach((q) => this.MyAnswers.push(null));

      this.MyHomeWork = response.MyHomeWork;
      if (this.MyHomeWork) {
        this.MyAnswers = this.MyHomeWork.Answers;
        response = await this.$api.getClassRoomByLessonID(this.$route.params.id);
        var room = response.ClassRoom;
        if (moment().isBetween(room.StartTime, room.EndTime)) {
          var index = room.Lessons.indexOf(parseInt(this.$route.params.id));
          var student = {
            IsLearning: true,
            Prev: null,
            Next: null,
          };
          if (index != -1) {
            if (index > 0) {
              student.Prev = room.Lessons[index - 1];
            }
            if (index < room.Lessons.length - 1) {
              student.Next = room.Lessons[index + 1];
            }
          }
          this.Student = student;
        } else {
          this.Student = {
            IsLearning: false,
          };
        }
      }
    },
    async submit() {
      for (var i = 0; i < this.HomeWork.Question.length; i++) {
        if (this.MyAnswers[i] == null) {
          alert(`請填寫第${i + 1}題`);
          return;
        }
      }
      var response = await this.$api.submitHomeWork(this.$route.params.id, {
        Answers: this.MyAnswers,
      });
      this.load();
    },
  },
};
</script>